<template>
    <div class="product-list">
      <div class="filters">
        <input 
          type="text" 
          v-model="searchQuery" 
          placeholder="Search products..."
        >
        <select v-model="selectedCategory">
          <option value="">All Categories</option>
          <option v-for="category in categories" :key="category">
            {{ category }}
          </option>
        </select>
        <select v-model="sortBy">
          <option value="price-asc">Price: Low to High</option>
          <option value="price-desc">Price: High to Low</option>
          <option value="name">Name</option>
        </select>
      </div>
  
      <div class="products-grid">
        <div v-for="product in filteredProducts" :key="product.id" class="product-card">
          <img :src="product.image" :alt="product.name">
          <h3>{{ product.name }}</h3>
          <p>{{ product.price }}</p>
          <div class="button-group">
            <button @click="addToCart(product)" class="btn-add">Add to Cart</button>
            <button @click="viewDetail(product.id)" class="btn-view">View Detail</button>
          </div>
        </div>
      </div>
  
      <ThePagination
        :current-page="currentPage"
        :total-pages="totalPages"
        @page-change="handlePageChange"
      />
    </div>
  </template>
  
  <script setup>
import { ref, computed } from 'vue'
import { useStore } from 'vuex'
import ThePagination from '../shared/ThePagination.vue'
import { useRouter } from 'vue-router'
const store = useStore()
const searchQuery = ref('')
const selectedCategory = ref('')
const sortBy = ref('price-asc')
const currentPage = ref(1)
const itemsPerPage = 12 // Changed to 12 items per page

const products = computed(() => store.getters['product/getAllProducts'])
const categories = computed(() => store.getters['product/getAllCategories'])

const filteredProducts = computed(() => {
  let result = [...products.value]
  
  if (searchQuery.value) {
    result = result.filter(p => 
      p.name.toLowerCase().includes(searchQuery.value.toLowerCase())
    )
  }
  
  if (selectedCategory.value) {
    result = result.filter(p => p.category === selectedCategory.value)
  }
  
  switch (sortBy.value) {
    case 'price-asc':
      result.sort((a, b) => a.price - b.price)
      break
    case 'price-desc':
      result.sort((a, b) => b.price - a.price)
      break
    case 'name':
      result.sort((a, b) => a.name.localeCompare(b.name))
      break
  }

  // Pagination logic
  const startIndex = (currentPage.value - 1) * itemsPerPage
  if (currentPage.value === 4) {
    return result.slice(-2) // Get only last 2 items for page 4
  }
  return result.slice(startIndex, startIndex + itemsPerPage)
})

const totalPages = computed(() => 4) // Fixed to 4 pages

const addToCart = (product) => {
  store.dispatch('cart/addItem', product)
}

const handlePageChange = (page) => {
  currentPage.value = page
}
const router = useRouter()
const viewDetail = (productId) => {
  router.push(`/products/${productId}`)
}
</script>
  <style scoped>
  .product-list {
    padding: 20px;
  }
  
  .filters {
    display: flex;
    gap: 15px;
    margin-bottom: 30px;
  }
  
  .filters input,
  .filters select {
    padding: 8px 12px;
    border: 1px solid #ddd;
    border-radius: 4px;
    min-width: 200px;
  }
  
  .products-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    margin-bottom: 30px;
  }
  
  .product-card {
    border: 1px solid #eee;
    border-radius: 8px;
    padding: 15px;
    text-align: center;
    transition: transform 0.2s;
  }
  
  .product-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  }
  
  .product-card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 4px;
    margin-bottom: 10px;
  }
  
  .product-card h3 {
    margin: 10px 0;
    font-size: 1.1rem;
  }
  
  .product-card p {
    color: #666;
    margin-bottom: 15px;
  }
  
  .product-card button {
    background: #3498db;
    color: white;
    border: none;
    padding: 8px 15px;
    border-radius: 4px;
    cursor: pointer;
    transition: background 0.2s;
  }
  
  .product-card button:hover {
    background: #2980b9;
  }

  .button-group {
  display: flex;
  gap: 10px;
  justify-content: center;
}

.btn-add, .btn-view {
  flex: 1;
  padding: 8px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.2s;
}

.btn-add {
  background: #3498db;
  color: white;
}

.btn-view {
  background: #2ecc71;
  color: white;
}

  </style>
  